<template>
<div class="layout">
  <div class="menu">
    <Menu :isCollapse="isCollapse"></Menu>
  </div>
  <div class="content" :class="{samll:isCollapse}">
    <Content @changShow="changShow" :isCollapse="isCollapse"></Content>
  </div>
</div>
</template>

<script>
import Menu from "@/views/layout/menu";
import Content from "@/views/layout/content";

export default {
  name: "index",
  components:{
    Menu,
    Content
  },
  data(){
    return{
      isCollapse:false
    }
  },
  methods:{
    changShow(){
      this.isCollapse =! this.isCollapse
    }
  }
}
</script>

<style lang="less" scoped>
.layout{
  //display: flex;
  .menu{
    background: #112f50;
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;

  }
  .content{
    //flex:1;
    padding-left: 200px;
  }
  .samll{
    padding-left: 65px;
    transition-property: all;
    transition-duration:1s;

  }
}
</style>
