import Vue from 'vue'
import VueRouter from 'vue-router'
import layout from "@/views/layout";
// import login from "@/views/login";
import Home from "@/views/Home";

Vue.use(VueRouter)

const routes = [
  {
    path: "/login",
    name: "login",
    meta: {
      // keepAlive: false,
      // isBack: false,
      // title: "登录页面"
    },
    component: (resolve) => require(['@/views/login'], resolve)
  },
  {
    path:'/',
    component:layout,
    children:[
      {
        path:'/',
        name:'home',
        component:Home
      },
      {
        path:'/product',//产品管理
        name:'product',
        component:()=>import('@/views/product'),
        children:[
          {
            path: 'list',
            name: 'list',
            component: () => import('@/views/product/list'),
          },
          {
            path: 'category',
            name: 'category',
            component: () => import('@/views/product/category'),
          }
        ]
      },
      {
        path:'/order',//订单管理
        name:'order',
        component:()=>import('@/views/order'),
        children:[
          {
            path: 'orderlist',
            name: 'orderlist',
            component: () => import('@/views/order/list'),
          },
          {
            path: 'collect',
            name: 'collect',
            component: () => import('@/views/order/collect'),
          }
        ]
      },
      {
        path:'/advert',//广告管理
        name:'advert',
        component:()=>import('@/views/advert'),
        children:[
          {
            path: 'advertlist',
            name: 'advertlist',
            component: () => import('@/views/advert/list'),
          },
        ]
      },

    ]
  },

  // {
  //   path:'/login',
  //   name:'login',
  //   component:login,
  // }

]

const router = new VueRouter({
  routes
})

export default router
